.loginFormPage {
    background-color: #FFFFFF;
    box-shadow: 0px 11px 35px 2px rgba(0, 0, 0, 0.14);
    width: 400px;
    height: 400px;
    margin: 7em auto;
    border-radius: 1.5em;
  }

  body {
    background-color: #F3EBF6;
  }

  #username, #password {
    text-align: center;
    width: 76%;
    padding: 10px 20px;
    box-sizing: border-box;
    margin-bottom: 50px;
    margin-bottom: 27px;
    margin-left: 46px;

    background: rgba(136, 126, 126, 0.04);
    color: rgb(38, 50, 56);

    font-family: 'Ubuntu', sans-serif;
    font-weight: 700;
    font-size: 14px;
    letter-spacing: 1px;

    border: none;
    border: 2px solid rgba(0, 0, 0, 0.02);
    border-radius: 20px;
    outline: none;
  }

  .heading {
    padding-top: 40px;
    font-family: 'Ubuntu', sans-serif;
    font-weight: bold;
    font-size: 23px;
  }
  